.div-button {
  border: 1px solid var(--dump-bcg-color);
  width: 100px;
  color: var(--light-theme-primary-text-color);
  letter-spacing: .04em;
  text-align: center;
  cursor: pointer;
  border-radius: 4px;
  padding: 12px 24px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
}

.top-books-list {
  flex-wrap: wrap;
  display: flex;
}

.top-book-card:nth-child(n+2) {
  display: none;
}

.top-book-card {
  width: 100%;
  border-radius: 8px;
  margin-left: auto;
  margin-right: auto;
}

.book-card-thumb {
  width: 335px;
  color: var(--light-theme-primary-text-color);
  font-family: inherit;
  text-decoration: none;
}

.book-card-thumb .thumb {
  margin-bottom: 14px;
}

.thumb {
  width: 100%;
  height: 485px;
  position: relative;
}

.thumb img {
  object-fit: fill;
  height: 100%;
  width: 100%;
  border-radius: 8px;
  display: block;
}

.book-cart-title {
  letter-spacing: -.64px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.book-card-author {
  color: var(--light-theme-second-text-color);
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
  .top-book-card:nth-child(-n+3) {
    display: block;
  }

  .books {
    margin-top: 98px;
    margin-bottom: 100px;
  }

  .category-name {
    letter-spacing: -1.92px;
    font-size: 48px;
    line-height: 1.083;
  }

  .books-container-list {
    column-gap: 25px;
  }

  .top-book-card {
    width: calc(33.3333% - 16.6667px);
  }

  .thumb {
    height: 316px;
  }
}

@media screen and (min-width: 1440px) {
  .top-book-card:nth-child(-n+5) {
    display: block;
  }

  .books {
    margin-top: 40px;
  }

  .books-container {
    width: 996px;
    padding: 0;
  }

  .category-name {
    letter-spacing: -1.92px;
    font-size: 48px;
    line-height: 1.083;
  }

  .book-card:-webkit-any(:hover, :focus) {
    transition: transform 250ms var(--transition-func);
    transition: box-shadow 250ms var(--transition-func);
    box-shadow: 0px 0px 11px -5px var(--light-theme-second-text-color);
    transform: scale(1.1);
  }

  .book-card:is(:hover, :focus) {
    transition: transform 250ms var(--transition-func);
    transition: box-shadow 250ms var(--transition-func);
    box-shadow: 0px 0px 11px -5px var(--light-theme-second-text-color);
    transform: scale(1.1);
  }

  .books-container-list {
    column-gap: 24px;
  }

  .top-book-card {
    width: calc(20% - 19.2px);
  }

  .thumb {
    height: 256px;
  }
}





